@import "../../main.style.scss";

.homepage {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  &__img {
    width: 100%;
    height: 37vw;
    background-image: url(./../../assets/home-page-long-image.jpg);
    background-size: cover;
    background-position: 50%;

    @include respond(desktop) {
      width: 70%;
      height: 25vw;
    }

    @include respond(big-desktop) {
      width: 120rem;
      height: 45rem;
    }
  }

  &__post {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    &--cards {
      grid-column: 2 / -2;
      width: 100%;
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
      gap: 2rem 3rem;
    }
  }
}
